.app__footer {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__footer-cards {
  width: 95%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap-reverse;
  margin: 4rem 2rem 2rem;

  .app__footer-card {
    min-width: 290px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    margin: 1rem 0;
    padding: 1rem;
    border-radius: 10px;
    cursor: pointer;
    background-color: #fef4f5;

    transition: all 0.3s ease-in-out;

    img {
      width: 40px;
      height: 40px;
      margin: 0 0.7rem;
    }

    p {
      font-weight: 600;
    }

    a {
      text-decoration: none;
      font-weight: 500;
    }

    &:hover {
      box-shadow: 0 0 25px #fef4f5;
    }

    @media screen and (max-width: 400px) {
      width: 60%;
    }
  }
  @media screen and (max-width: 800px) {
    width: 90%;
  }
}

.app__footer-cards .app__footer-card:last-child {
  background-color: f2f7fb;
}

.app__footer-form {
  width: 90%;
  flex-direction: column;
  margin: 1rem 2rem;

  div {
    width: 50%;
    margin: 0.75rem 0;
    border-radius: 10px;
    cursor: pointer;
    background-color: var(--primary-color);
    transition: all 0.3s ease-in-out;

    input,
    textarea {
      width: 100%;
      padding: 0.95rem;
      border: none;
      border-radius: 8px;
      background-color: #fef4f5;
      font-family: var(--font-base);
      color: var(--secondary-color);
      outline: none;
    }

    @media screen and (max-width: 600px) {
      width: 90%;
    }
    textarea {
      height: 170px;
    }
    &:hover {
      box-shadow: 0 0 25px #fef4f5;
    }
  }
  button {
    padding: 1rem 2rem;
    border-radius: 10px;
    border: none;
    background-color: var(--secondary-color);
    font-weight: 500;
    color: var(--white-color);
    outline: none;
    margin: 2rem 0 0 0;
    font-family: var(--font-base);
    cursor: pointer;
  }
}
