#home {
  position: relative;
  background: url("../../assets/bgIMG.png");
  background-size: cover;
  background-repeat: repeat;
  background-position: center;

  .app__wrapper {
    padding: 0;
    margin: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 4rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1100px) {
    flex-direction: column;
    // padding: 5rem 2rem 0;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }

  @media screen and (max-width: 450px) {
    padding: 3rem 0.5rem 0.8rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  @media screen and (max-width: 2000px) {
    width: 100%;
    margin-right: 0rem;
  }

  //    @media screen and (max-width: 600px) {
  //     width: 100%;
  //     margin-right: 5rem;
  //     justify-content: flex-end;
  //     align-items: flex-start;

  //   }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  margin-top: 2rem;
  z-index: 2;

  .badge-cmp,
  .tag-cmp {
    padding: 1.2rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    margin-bottom: 1rem;

    box-shadow: 0 0 20px rgba(0, 0, 0, 0.16);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 0rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: right;
    }
  }

  span {
    font-size: 2.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1100px) {
    width: 0;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left: 6rem;
    margin-bottom: 2rem;
    margin-right: 4rem;
    margin-top: 7rem;
  }

  @media screen and (max-width: 500px) {
    width: 0;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 2rem;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  margin-top: 5rem;
  margin-left: 3rem;

  div {
    margin: 1rem;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }

    @media screen and (min-width: 2000px) {
      margin: 2rem;
      margin-bottom: 4rem;
    }
  }

  div:nth-child(1) {
    width: 130px;
    height: 130px;
  }
  div:nth-child(2) {
    width: 190px;
    height: 190px;
  }
  div:nth-child(3) {
    width: 100px;
    height: 100px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 200px;
      height: 200px;
    }
    div:nth-child(2) {
      width: 320px;
      height: 320px;
    }
    div:nth-child(3) {
      width: 160px;
      height: 160px;
    }
  }

  @media screen and (max-width: 1100px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 80%;
    object-fit: contain;
    z-index: 1;
    margin-top: 7rem;

    @media screen and (max-width: 650px) {
      width: 100%;
      margin-top: 1.5rem;
    }
  }

  .overlay_circle {
    position: absolute;

    left: 0;
    right: 1rem;
    bottom: 0;
    z-index: 0;
    width: 90%;
    height: 90%;
  }

  @media screen and (max-width: 1100px) {
    margin: 2rem 0;
  }
}
