.app__skills {
  flex: 1;
  width: 100%;
  flex-direction: column;
  margin-top: 5rem;
}
.m {
  padding-top: 4rem;
}

.app__skills-container {
  width: 80%;
  margin-top: 3rem;
  margin-left: 8rem;
  margin-right: 4rem;

  display: flex;
  flex-direction: row;

  @media screen and (max-width: 1000px) {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    flex-direction: column;
  }
}

.app__skills-list {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;

  margin-right: 5rem;

  @media screen and (max-width: 1000px) {
    margin-left: 0;
    margin-right: 0;
    justify-content: center;
    align-items: center;
  }
}

.app__skills-item {
  flex-direction: column;
  text-align: center;
  margin: 1rem;
  transition: all 0.3s ease-in-out;

  div {
    width: 75px;
    height: 75px;
    border-radius: 50%;
    background-color: #fff;

    img {
      width: 50%;
      height: 50%;
    }

    &:hover {
      box-shadow: 0 0 25px #d0d0d0;
    }

    // @media screen and (min-width: 2000px) {
    //     width: 110px;
    //     height: 110px;
    // }

    @media screen and (max-width: 600px) {
      width: 50px;
      height: 50px;
    }
  }

  p {
    font-weight: 500;
    margin-top: 0.5rem;
  }

  @media screen and (min-width: 2000px) {
    margin: 1rem 2rem;

    p {
      margin-top: 1rem;
    }
  }
}

.app__skills-exp {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 6rem;

  @media screen and (max-width: 1000px) {
    margin-top: 2rem;
    margin-left: 3rem;
  }
}

.app__skills-exp-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.app__skills-exp-works {
  flex: 1;

  .app__skills-exp-work {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 1rem;
    cursor: pointer;
  }

  h4 {
    font-weight: 500;
  }

  p {
    font-weight: 400;
    color: var(--gray-color);
    margin-top: 5px;
  }
}

.app__skills-exp-year {
  margin-right: 3rem;

  p {
    font-weight: 800;
    color: var(--secondary-color);
  }

  @media screen and (max-width: 450px) {
    margin-right: 1rem;
    margin-left: 1rem;
  }
}
